/* popup positioned absolutely */
.chat-window {
  background-color: #CCEFEB;
  border-radius: 4px;
  position: absolute;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  overflow: hidden;
}

.close-button {
  position: absolute;
  top: 0px;
  right: 0px;
  padding: 2px;
  margin-right: 15px;
  margin-top: 0px;
  cursor: pointer;
  color: #337ab7;
}

.content {
  height: calc(100%);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  padding: 16px 4px 4px 4px;
}

.messages-panel {
  border-radius: 4px;
  height: calc(100%);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  overflow-y: scroll;
  overflow-x: hidden;
  margin: 10px;
  background-color: #FFFFFF;
}

.message-panel-bottom {
  width: calc(100%);
  margin: 15px;
}

div.sent-message {
    margin-left:auto;
    margin-right:5px;
    width: auto;
    padding: 2px;
}

div.message {
    width: auto;
    margin: 0px;
    padding: 2px;
    width: 98%;
}

div.message-body {
    background: #DDEEDD;
    margin: 5px;
    padding: 5px;
    border-radius: 4px;
    width: auto;
    display: inline-block;
}

div.sent-message-body {
    background: #DDDDDD;
    margin: 5px;
    padding: 5px;
    border-radius: 4px;
    width: auto;
    display: inline-block;
}

div.message-nick {
    margin-left: 15px;
    font-size: smaller;
}

div.sent-message-nick {
    margin-right: 15px;
    font-size: smaller;  
    text-align: right;
}

div.spacer {
  -ms-flex-positive: 1;
      flex-grow: 1;
}

.input-panel {
    width: calc(100%);
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    padding: 5px;
}

.input-nick {
    width: 125px;
    margin-left: 5px;
    margin-right: 5px;
}

.view-nick {
    width: 125px;
    margin-left: 5px;
    margin-right: 5px;
    cursor: pointer;
    background-color: #FCFCFC;
    text-align: center;
    border-radius: 4px;
    padding-top: 2px;
}

.input-message {
    -ms-flex-positive: 1;
        flex-grow: 1;
}

.button-text-direction {
  margin-top: 2px;
  margin-right: 4px;
  cursor: pointer;
  color: #337ab7;
}

.horizontal-flip {
  -webkit-transform: scaleX(-1);
      -ms-transform: scaleX(-1);
          transform: scaleX(-1);
}

.button-send {
    width: 60px;
    margin-left: 5px;
    cursor: pointer;
    text-align: center;
    padding-right: 10px;
    color: #337ab7;
}



