.navigation-bar {
}

.content-panel {
  height: calc(100%);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.chat-button {
    position: relative;
    float: right;
    z-index: 1200;
    cursor: pointer;
    color: #337ab7;
}

.chat-icon {
    padding-top: 11px;
}

.chat-offline {
    color: grey;
    cursor: auto;
}

#scroll-to-top {
    position: fixed;
    z-index: 2000;
    right: 30px;
    top: 1.25em;
}

@-webkit-keyframes shake {
    from { -webkit-transform: translateY(-4px); transform: translateY(-4px); }
    to { -webkit-transform: translateY(0px); transform: translateY(0px); }
}

@keyframes shake {
    from { -webkit-transform: translateY(-4px); transform: translateY(-4px); }
    to { -webkit-transform: translateY(0px); transform: translateY(0px); }
}

@-webkit-keyframes flash-on {
    from { opacity: 0; visibility: hidden; }
    to { opacity: 1; visibility: visible; }
}

@keyframes flash-on {
    from { opacity: 0; visibility: hidden; }
    to { opacity: 1; visibility: visible; }
}

@-webkit-keyframes flash-off {
    to { opacity: 0; visibility: hidden; }
    from { opacity: 1; visibility: visible; }
}

@keyframes flash-off {
    to { opacity: 0; visibility: hidden; }
    from { opacity: 1; visibility: visible; }
}

.chat-mention {
    -webkit-animation-name: shake;
            animation-name: shake;
    -webkit-animation-duration: .3s;
            animation-duration: .3s;
    -webkit-animation-direction: alternate;
            animation-direction: alternate;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-timing-function: cubic-bezier(.52,.4,.75,-0.01);
            animation-timing-function: cubic-bezier(.52,.4,.75,-0.01);
}

.chat-new-message-on {
    -webkit-animation-name: flash-on;
            animation-name: flash-on;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-direction: alternate;
            animation-direction: alternate;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-timing-function: ease-in-out;
            animation-timing-function: ease-in-out;
}

.chat-new-message-off {
    -webkit-animation-name: flash-off;
            animation-name: flash-off;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-direction: alternate;
            animation-direction: alternate;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-timing-function: ease-in-out;
            animation-timing-function: ease-in-out;
}

a {
    text-decoration: none !important;
}



